@import "./variables";

.bugs_wrap {
    display: flex;
    flex-direction: column;
    flex: 1;
    max-width: 40%;
    background: $ui-x-dark;
    padding: 3rem 2rem;
}

.bug_wrap {
    cursor: pointer;
    background: $ui-med;
    padding: 1rem;
    border-radius: 0.5rem;
    margin-bottom: 0.5rem;
    color: white;
    box-shadow: $shadow-low;
}

.bug_wrap.is_open {
    background: $purple;
}

.bug_wrap.is_open:hover {
    background: $purple;
}

.bug_wrap:hover {
    background: $ui-highlight;
}

.bug_body_wrap {
    opacity: 0;
    animation: fade-in 0.6s cubic-bezier(0.5, 1, 0.89, 1) forwards;
}

.bug_row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.bug_body_row {
    margin-bottom: 0.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid $ui-x-dark;
}

.bug_body_row.error_row {
    // border-bottom: none;
    padding-bottom: 0;
}

.title_row {
    h3 {
        opacity: 0.5;
        font-family: $font-accent;
        font-size: 0.8rem;
    }
    :hover {
        color: $green-bright;
    }
}

.bug_title_wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.bug_title {
    margin-left: 0.5rem;
    text-overflow: ellipsis;
    transition: all 0.2s cubic-bezier(0.5, 1, 0.89, 1);
}

.severity {
    height: 8px;
    width: 8px;
    border-radius: 50%;
    display: inline-block;
    transform: translateX(0px);
    animation: fade-in 0.4s cubic-bezier(0.5, 1, 0.89, 1) forwards;
}

.severity_label {
    color: $ui-dark;
    padding: 0.25rem;
    border-radius: 0.25rem;
    font-family: $font-accent;
    font-size: 0.8rem;
    font-weight: 700;
}

.is_open {
    .bug_title_wrap > .severity {
        animation: slide-off 0.4s cubic-bezier(0.25, 1, 0.5, 1) forwards;
    }
    .title_row h2 {
        // font-size: 1.4rem;
        margin-left: 0;
        // margin-bottom: 0.5rem;
        // color: $green-bright;
        text-shadow: $text-shadow;
        transition: all 0.2s cubic-bezier(0.5, 1, 0.89, 1);
    }
    .title_row h3 {
        opacity: 1;
        text-shadow: $text-shadow;
    }
}

.bug_error_readout {
    background: $ui-x-dark;
    padding: 1rem;
    border-radius: 0.25rem;
    color: $red;
    font-family: $font-accent;
    margin: 0.5rem 0 1rem 0;
}

.bug_label {
    opacity: 0.5;
    display: block;
    margin: 0.25rem 0;
}

.bug_controls_wrap {
    margin-top: 1rem;
}

.edit_button {
    margin-right: 0.5rem;
}

@media screen and (max-width: $br-large) {
    .bugs_wrap {
        display: flex;
        max-width: 100%;
    }
    .title_row h3 {
        display: none;
    }
}

@media screen and (max-width: $br-med) {
    .bugs_wrap {
        display: none;
    }
}
